import React, {useRef} from 'react';
import ContactCard from './ContactCard';
import {Link} from 'react-router-dom';


const ContactList=(props)=>
{const inputel=useRef("");

    const getContactId=props.getContactId;
const deleteContactHandler=(id)=>{
    getContactId(id);

}


const contacts=props.contacts;
    const renderContactList=contacts.map((contact)=>{
       return(
        <ContactCard contact={contact} clickHandler={deleteContactHandler} key={contact.id} />
       );
    });

    const getSearchTerm=()=>{
        props.searchKeyword(inputel.current.value);
    }

    return(
        <div className="main">

        
        <div><h1> Contact List</h1></div>

        
            <div className="ui search">
            <div className="ui icon input">
                <input 
                ref={inputel}
                type="text" placeholder="Search for a Contact" className="prompt" value={props.term} onChange={getSearchTerm}/>
                <i  className="search icon"></i>
            </div>
        </div>
            <Link to="/add">
            <button className="ui button blue right">Add Contact</button></Link>
        
        <div className="ui celled list">{renderContactList}</div>
</div>
    );
}
export default ContactList; 